@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap');

$main-font:'Source Sans Pro', sans-serif;

$track-background:#CDCDCD;
$scroll-background:rgb(26, 26, 26);
$scroll-gradient:linear-gradient(180deg, rgba(50, 50, 50, .86) 0%, rgba(26, 26, 26, 1) 100%);

$header-background:rgb(17,17,17);
$header-gradient:linear-gradient(90deg, rgba(17,17,17,1) 0%, rgba(50,50,50,0.87) 100%);

$menu-background:rgb(50, 50, 50);
$menu-gradient:linear-gradient(45deg, rgba(50, 50, 50, 1) 85%, rgba(161, 161, 161, 0.7) 100%);

$toggle-menu-background:rgb(0, 161, 156);
$toggle-menu-gradient:linear-gradient(90deg,rgba(0, 161, 156, 1) 0%,rgba(0, 161, 156, 0.56) 100%);

$article-background:rgb(50, 50, 50);
$article-gradient:linear-gradient(180deg, rgba(50, 50, 50, 0.8) 0%, rgba(50, 50, 50, 0) 100%);

$m-white:#DADADA;
$contrast:#10C5C0;
$padrao-button:.3s;
