@import 'config';
@import 'keyframes';
@import 'queries';

footer {
  width:100vw;
  min-height:30rem;
  background:#232323;
  -webkit-clip-path: polygon(0 14%, 100% 0, 100% 100%, 0% 100%);
  clip-path: polygon(0 14%, 100% 0, 100% 100%, 0% 100%);
  margin-top:-6rem;
  margin-right:-22rem;
  display:grid;
  place-items:center;
  padding:5rem 0 6rem .2rem;

  a {
    margin-top:1.5rem;
    color:$m-white;
    opacity:.8;
    transition:.3s;

    &:hover {
      text-decoration:none;
      transition:.3s;
      opacity:1;
    }
  }
}

.container {
  display:flex;
  justify-content:space-between;
  color:$m-white;
  margin-left:6.2rem;
  margin-top:3rem;
  width:80%;
  height:17rem;

  .perguntas-frequentes, span {
    font-size:.875rem;
  }

  h1 {
    font-size:1.25rem;
  }

  span {
    opacity:.8;
  }

  .perguntas-frequentes {
    display:grid;
    height:17rem;

    a {
      width:8.5rem;
    }

    a:first-child {
      margin-top:.2rem;
    }
  }

  .newsletter {
    display:grid;
    grid-template-rows:repeat(25, 1rem);
    gap:.7rem;

    h1 {
      font-weight:600;
    }

    .email-error {
      display:none;
    }

    .email-error.active {
      position:absolute;
      margin-top:7.2em;
      background:#F1F1F1;
      color:#403607;
      display:flex;
      gap:.5rem;
      padding:.2rem .4rem;
      font-weight:bold;
      border-radius:.2rem;
      border:.2rem solid #FFD91D;
      animation:show-error 4s forwards;

      &:after {
        content:'';
        position:absolute;
        width:.75rem;
        height:.75rem;
        background:linear-gradient(45deg,rgba(255, 217, 29, 0) 50%,rgba(255, 217, 29, 1) 50%);
        margin-top:-.77rem;
        margin-left:.3rem;
        transform:rotate(-45deg);
      }
    }

    input {
      border-radius:.4rem 0 0 .4rem;
      background:#CDCDCD;
      padding:1.5rem .75rem;
      font-size:1rem;
      height:1.5rem;

      &:focus {
        outline:0;
      }
    }

    button {
      background:linear-gradient(90deg,rgba(39, 194, 128, 1) 0%,rgba(39, 194, 128, 0.1) 100%) var(--x, 0) / 200%;
      justify-self:center;
      position:absolute;
      border-radius:0 .4rem .4rem 0;
      margin-left:12.61rem;
      margin-top:-3rem;
      padding:.58rem;
      height:3rem;
      cursor:pointer;
      transition:.8s;
      display:flex;
      width:3rem;
      gap:.5rem;
      display:flex;
      align-items:center;

      &:hover {
        --x:-200%;
      }
    }
  }

  button.checked:after {
    content:'';
    position:absolute;
    margin-left:-13.2rem;
    width:0rem;
    height:3.1rem;
    border-radius:.4rem 0 0 .4rem;
    background:rgba(39, 194, 128, 0.8);
    z-index:2;
    transition:.3s;
    animation:anima-email 1s forwards;
  }

  .feedback {
    width:17.5rem;

    span {
      display:block;
      margin-top:.3rem;
    }

    a {
      margin-left:.2rem;
      opacity:1;
    }
  }
}
