/* header */
@keyframes back-arrow {
  from {
    transform: translateX(.3rem);
  }

  to {
    transform: translateX(0);
  }
}

@keyframes go-arrow {
  from {
    transform:translateX(0);
  }

  to {
    transform: translateX(.3rem);
  } 
}

@keyframes show-detail {
  from {
    width: 0rem;
  }

  to {
    width: 1rem;
  }
}

@keyframes hides-detail {
  from {
    width: 1rem;
  }

  to {
    width: 0rem;
  }
}

//menu {
@keyframes show-menu {
  from {
    opacity:0;
    transform:translateY(1rem);
  }

  to {
    opacity:1;
    transform:translateY(0rem);
  }
}

@keyframes hides-menu {
  from {
    opacity:1;
    transform: translateY(0rem);
  }

  to {
    opacity:0;
    transform: translateY(1rem);
  }
}

//Saiba mais
@keyframes background-blur {
  from {
    filter:blur(0);
  }

  to {
    filter:blur(.1rem);
  }
}

@keyframes show-main-container {
  from {
    opacity:0;
    transform:translateY(-1.8rem);
  }

  to {
    opacity:1;
    transform:translateY(0);
  }
}

@keyframes show-main-button {
  from {
    opacity:0;
    transform:translateY(2rem);
  }

  to {
    opacity:1;
    transform:translateY(0);
  }
}

//curiosidades
@keyframes show-curiosidade {
  from {
    opacity:0;
    transform:translateX(-2rem);
  }

  to {
    opacity:1; 
    transform:translateX(0);
  }
}

//footer 
@keyframes anima-email {
  from {
    width:0rem;
    opacity:1;
  }

  50% {
    opacity:.7;
  }

  to {
    opacity:0;
    width:14.6rem;
  }
}

@keyframes show-error {
  0% {
    transform:translateY(.5rem);
    opacity:1;
  }

  15% {
    transform:translateY(0);
    opacity:1;
  }

  50% {
    transform:translateY(0);
    opacity:1;
  }

  100% {
    opacity:0;
    display:none;
  }
}
